#root{
  display: flex;
  flex-direction: column;
}
header{
  position: sticky;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1e293b;
  color: rgba(255, 255, 255, 0.856);
}

main{
  margin: 20px;
  padding: 20px;
}

.main-section{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0px;
}

.code-snippet{
  display: flex;
  flex-direction: column;
  width:650px;
  text-overflow: ellipsis;
  white-space:nowrap;
  overflow:hidden;
  text-decoration: none;
  background: #1e293b;
  color:rgba(255, 255, 255, 0.9);
  border-radius: 10px;
}
.copy-button-cont{
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}

.copy-button{
  background: transparent;
  border:none;
  padding: 10px;
}
.copy-button:hover{
  opacity: 0.8;
}
form{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.input-container{
  display: flex;
  margin-bottom: 25px;
}
.input-container-item{
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}
.checkbox-container{
  display: flex;
  flex-direction: column;
}
input[type="text"]{
  padding: 5px;
}
input[type="checkbox"]{
  margin-left: 20px;
  padding: 20px;
  margin-bottom: 10px;
}


